* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* color Pallete */
:root {
  --darkSienna: #420C14;
  --wine: #732C2C;
  --mistyMoss: #aba361;
  --celadon: #bad9b5;
  --lightGoldenRodYelloew: #eff7cf;
}

/* Nav styling */
.navbar--wrapper {
  width: 100%;
  padding-inline: 50px;
  padding-block: 15px;
  font-size: 1rem;

  background: linear-gradient(90deg, var(--darkSienna) 1.18%, var(--wine) 100%);

  display: flex;
  align-items: center;
  justify-content: space-between;
}


.navbar--wrapper .nav--logo img {
  width: 55px;
  height: 50px;
  margin-inline-end: 10px;
}

.nav--menu {
  color: var(--lightGoldenRodYelloew);
}

.nav--menu .nav--title {
  font-size: clamp(1.2rem, 2.5vw, 2rem);
}

.nav--menu .nav--items {
  width: 100%;

  font-size: clamp(1rem, 2.5vw, 1.1rem);

  display: flex;
  justify-content: space-between;
}

/* App */
.app--wrapper {
  width: 100%;
  min-height: 100vh;

  background-color: var(--mistyMoss);
}

/* meme */
.meme--wrapper {
  width: 70%;

  margin-inline: auto;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.memeTxt--inputField {
  width: 100%;

  display: flex;
  justify-content: space-between;

  margin-block-start: 50px;
  margin-block-end: 30px;
}

.m--inpt {
  width: 49.5%;
  padding-block: 15px;
  background-color: var(--celadon);

  font-size: clamp(1rem, 2.5vw, 1.1rem);
  text-indent: 8px;

  outline: none;
  border-radius: 5px;
  border: 1px solid var(--lightGoldenRodYelloew);
}

.m--inpt::placeholder {
  font-style: italic;
  color: var(--lightGoldenRodYelloew);
}

.getImgBtn {
  width: 100%;
  padding-block: 15px;
  margin-block-end: 30px;

  border: 0;
  outline: none;
  border-radius: 5px;
  font-size: clamp(1rem, 2.5vw, 1.1rem);

  color: var(--lightGoldenRodYelloew);
  background: linear-gradient(90deg, var(--darkSienna) 1.18%, var(--wine) 100%);

}

.meme--img {
  width: 600px;
  height: 400px;



  position: relative;
}

.meme--img .Ttxt {
  position: absolute;
  top: 0;
}

.meme--img .memeTxt {
  position: absolute;
  width: 80%;
  text-align: center;
  left: 50%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2em;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:
    2px 2px 0 #000,
    -2px -2px 0 #000,
    2px -2px 0 #000,
    -2px 2px 0 #000,
    0 2px 0 #000,
    2px 0 0 #000,
    0 -2px 0 #000,
    -2px 0 0 #000,
    2px 2px 5px #000;
}

.meme--img .Btxt {
  position: absolute;
  bottom: 0;
}

.meme--img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media screen and (max-width:780px) {
  .navbar--wrapper {
    padding-inline: 8px;
  }

  .meme--img {
    width: 100%;
  }
}